import React from 'react';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';
import './App.css';
import { toast, ToastContainer, Zoom } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import About from './components/About/About';
import Home from './components/Home/Home';
import {AuthProvider} from './contexts/AuthContext';
import { getAuth } from "firebase/auth";
import SinglePost from './components/SinglePost/SinglePost';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Register from './components/Register/Register';
import Login from './components/Login/Login';
import Blog from './components/Blog/Blog';
import Create from './components/Create/Create';
import Edit from './components/Edit/Edit';
import MyPosts from './components/MyPosts/MyPosts';
export const auth = getAuth();

function App() { 
  return (
    <AuthProvider>

    <BrowserRouter>
      <div className="App">
        <Header></Header>
        <ToastContainer transition={Zoom} autoClose={2000}/>

        <Route exact path="/noveauriche" component={Home } />
        <Route path="/noveauriche/about" component={About} />
        <Route path="/noveauriche/blog" component={Blog} />
        <Route path="/noveauriche/my-posts" component={MyPosts} />
        <Route path="/noveauriche/create" component={Create} />
        <Route path="/noveauriche/edit" component={Edit} />
        <Route path="/noveauriche/single-post" component={SinglePost} />
        <Route path="/noveauriche/register" component={Register} />
        <Route path="/noveauriche/login" component={Login} />
        <Route path="/noveauriche/logout" render={() => {
            auth.signOut();
            toast.success("Logged out!", {
              
            })
            return <Redirect to="/noveauriche/login" />
          }} />

        <Footer></Footer>
      </div>
    </BrowserRouter>
    </AuthProvider>

  );
}

export default App;
